import React ,{Component} from 'react';
import '../css/setting.css';
import { Link } from 'react-router-dom';
export default class Setting extends Component{
    constructor(props){
        super(props);
        this.state={};
    }

    render(){
        return(
            <div>
                 <div class="backHeader">
                    <span>设定</span>
                </div>
                <a href="/EditMessage"><div class="settingHeader">
                    <img src={require('../images/34.png')} class="ownImg" alt=""/>
                    <div class="personMessage">
                        <div>香港权志龙</div>
                        <div>13854587546</div>
                    </div>
                    <div class="messageEdit">
                        <img src={require('../images/35.png')} alt=""/>
                        <div>编辑资料</div>
                    </div>
                </div></a>
                
                <a href="/AdminMessage"><div class="settingAdmin">
                    <div>
                         <span class="adminTitle">我的会员</span>
                         <span class="adminDes">查看详情<img src={require('../images/36.png')} alt=""/></span>
                    </div>
                   
                    <div class="settingAdminInnerWrap">
                        <div class="settingAdminInner">
                            <div>Level 2</div>
                            <div>会员等级</div>
                            <span class="lines"></span>
                        </div>
                        <div class="settingAdminInner">
                            <div>20</div>
                            <div>剩余次数</div>
                            <span class="lines"></span>
                        </div>
                        <div class="settingAdminInner">
                            <div>2020/05/06</div>
                            <div>到期日期</div>
                        </div>
                    </div>
                </div></a>

                <div class="borderArea"></div>
                
                <a href="/Ticket"><div class="settingInner">
                    <img src={require('../images/37.png')} alt="" class="limg"/>
                    <span>购票记录</span>
                    <img src={require('../images/42.png')} alt="" class="rjt"/>
                </div></a>
                <a href="/Pay"> <div class="settingInner">
                    <img src={require('../images/38.png')} alt="" class="limg"/>
                    <span>付款资料</span>
                    <img src={require('../images/42.png')} alt="" class="rjt"/>
                </div></a>
                <a href="/Record"><div class="settingInner">
                    <img src={require('../images/39.png')} alt="" class="limg"/>
                    <span>充值记录</span>
                    <img src={require('../images/42.png')} alt="" class="rjt"/>
                </div></a>
                <a href="/Recomm"><div class="settingInner">
                    <img src={require('../images/40.png')} alt="" class="limg"/>
                    <span>推荐有赏</span>
                    <img src={require('../images/42.png')} alt="" class="rjt"/>
                </div></a>
                <a href="/focusSetting"><div class="settingInner">
                    <img src={require('../images/41.png')} alt="" class="limg"/>
                    <span>关注设定</span>
                    <img src={require('../images/42.png')} alt="" class="rjt"/>
                </div></a>
                <div class="exitLogin">注销登录</div>
            </div>
        )
    }
}