import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/search.css';
export default class Search_3 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }

    render() {
        return (
            <div>
                <div class="backHeader">
                    <Link to="/Search_2"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                    <span>搜索</span>
                </div>
                <div class="searchWrap">
                    <div class="searchInner">
                        <img src={require('../images/31.png')} alt=""/>
                        <input type="text" value="BTS為挑戰小戴自殺死" />
                    </div>
                    <span class="cancel cancelSearch">搜索</span>
                </div>
               
                <div class="searchTitleWrap bt0">
                    <div class="searchTitleH">搜索结果</div>
                    <img src={require('../images/33.png')} alt="" class="noneImg"/>
                    <div class="noneWord">没有相关资料</div>
                </div>
            </div>
        )


    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
}