import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/pay.css';
export default class Pay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false,
            menuStatus: false
        };
    }

    render() {
        return (
            <div>
                <div class="backHeader">
                    <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                    <span>付款资料</span>
                </div>
                <div class="ownMessageInputWrap">
                    <div class="ownMessageInput">
                        <div class="inputTop">姓名</div>
                        <input type="text" placeholder="陈达文"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">电话号码</div>
                        <input type="text" placeholder="+18595854521"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">邮箱</div>
                        <input type="text" placeholder="397442219@qq.com"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">地区</div>
                        <input type="text" placeholder="请选择你的地区"/>
                        <div class="sDiv">
                            <span onClick= {this.menuChange.bind()}>地区1</span>
                            <img src={require('../images/15.png')} onClick= {this.menuChange.bind()}  alt=""/>
                            {this.state.menuStatus===true ?<div class="menuDiv">
                                <div onClick= {this.menuCancelChange.bind()}>地区一</div>
                                <div onClick= {this.menuCancelChange.bind()}>地区二</div>
                                <div onClick= {this.menuCancelChange.bind()}>地区三</div>
                                <div onClick= {this.menuCancelChange.bind()}>地区四</div>
                            </div>:''}
                            
                        </div>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">地址</div>
                        <input type="text" placeholder="请填写你的地址"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">取票密码</div>
                        <input type="text" placeholder="123456"/>
                    </div>
                </div>
                
                <div class="ownMessageInputWrap btt">
                    <div class="bttTop"><img src={require('../images/38.png')} alt=""/><span>信用卡资料</span></div>
                    <div class="ownMessageInput">
                        <div class="inputTop">持卡人姓名</div>
                        <input type="text" placeholder="陈达文"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">信用卡号</div>
                        <input type="text" placeholder="+430602112225454"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">到期日</div>
                        <div class="dateWrap">
                            <div class=" sDiv_1">
                                <span>07</span>
                                <img src={require('../images/15.png')}  alt=""/>
                            </div>
                            <span>月---</span>
                            <div class=" sDiv_1">
                                <span>2020</span>
                                <img src={require('../images/15.png')}  alt=""/>
                            </div>
                            <span>年</span>
                        </div>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">CVV</div>
                        <input type="text" placeholder="1234"/>
                    </div>
                </div>
                <div class="exitLogin" >保存</div>
                <div class="exitLogin pinkBg" >清除所有资料</div>
            </div>
        )


    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
    menuCancelChange= () =>{
        this.setState({menuStatus: false});
    }
    menuChange= () =>{
        this.setState({menuStatus: true});
    }
    
}