import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/record.css';
export default class Ticket extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }

    render() {
        return (
            <div>
                <div class="backHeader">
                    <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                    <span>充值记录</span>
                </div>
                
                <div class="recordWrap">
                    <img src={require('../images/49.png')}  alt=""/>
                    <div class="typeContent">
                        <div class="t_1">LV2 收费会员</div>
                        <div class="t_2"><span>包季</span><span>数量：2</span></div>
                        <div class="t_3">购买于 : 2020/01/02 13:56</div>
                        <div class="t_4">$29.9</div>
                    </div>
                    
                </div>
                <div class="recordWrap marTop0">
                    <img src={require('../images/49.png')}  alt=""/>
                    <div class="typeContent">
                        <div class="t_1">LV2 收费会员</div>
                        <div class="t_2"><span>包季</span><span>数量：2</span></div>
                        <div class="t_3">购买于 : 2020/01/02 13:56</div>
                        <div class="t_4">$29.9</div>
                    </div>
                    
                </div>
                <div class="norecord">没有更多记录</div>
            </div>
        )


    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
}