import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/setting.css';
export default class EditMessage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus_1: false,
            btnStatus_2: false,
            menuStatus: false
        };
    }

    render() {
        return (
            <div>
                {this.state.btnStatus_1===true ?<div><div class="fade"></div>
                <div class="fade_pop_o">
                    <div class="fade_pop_ow1">提示</div>
                    <img src={require('../images/58.png')} class="fade_pop_owimg1" />
                    <div class="fade_pop_ow2">信息修改失败 </div>
                    <div class="fade_pop_ow2">请检查所输入资料是否正确</div>
                    <div class="fade_pop_ow3" onClick= {this.fadeChange_cancel.bind()}>知道了</div>
                </div></div>:''}
                {this.state.btnStatus_2===true ?<div><div class="fade"></div>
                <div class="fade_pop_o">
                    <div class="fade_pop_ow1">提示</div>
                    <img src={require('../images/58.png')} class="fade_pop_owimg1" />
                    <div class="fade_pop_ow2">信息修改成功</div>
                    <div class="fade_pop_ow3" onClick= {this.fadeChange_cancel_2.bind()}>知道了</div>
                </div></div>:''}
                <div class="backHeader">
                    <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                    <span>编辑资料</span>
                </div>
                
                <div class="ownMessageImg">
                    <img src={require('../images/34.png')}  alt=""/>
                    <div>更换头像</div>
                </div>
                <div class="ownMessageInputWrap">
                    <div class="ownMessageInput">
                        <div class="inputTop">电话号码</div>
                        <input type="text" placeholder="+861825854547"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">别名</div>
                        <input type="text" placeholder="香港智能-小戴狗"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">地区</div>
                        <input type="text" placeholder="请选择你的地区"/>
                        <div class="sDiv">
                        <span onClick= {this.menuChange.bind()}>地区1</span>
                            <img src={require('../images/15.png')} onClick= {this.menuChange.bind()}  alt=""/>
                            {this.state.menuStatus===true ?<div class="menuDiv">
                                <div onClick= {this.menuCancelChange.bind()}>地区一</div>
                                <div onClick= {this.menuCancelChange.bind()}>地区二</div>
                                <div onClick= {this.menuCancelChange.bind()}>地区三</div>
                                <div onClick= {this.menuCancelChange.bind()}>地区四</div>
                            </div>:''}
                        </div>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">支付宝账号<font>(用于体现，不能修改)</font></div>
                        <input type="text" placeholder="huang18545125452"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">更改密码</div>
                        <input type="text" placeholder="请输入你的新密码"/>
                    </div>
                </div>
                <div class="exitLogin" onClick= {this.fadeChange_1.bind()}>确定修改</div>
            </div>
        )


    }
    fadeChange_1= () => {
        this.setState({btnStatus_1: true});
    }
    fadeChange_2= () => {
        this.setState({btnStatus_2: true});
    };
    fadeChange_cancel= () =>{
        this.setState({btnStatus_1: false,btnStatus_2: true});
    }
    fadeChange_cancel_2= () =>{
        this.setState({btnStatus_2: false});
    }
    menuCancelChange= () =>{
        this.setState({menuStatus: false});
    }
    menuChange= () =>{
        this.setState({menuStatus: true});
    }
}