import React, { Component } from 'react';
import '../css/login.css';
import '../css/common.css';
import { Link } from 'react-router-dom';
export default class Reset extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }

    render() {
        return (
            <div>
                {this.state.btnStatus===true ?<div><div class="fade"></div>
                <div class="fade_pop_o">
                    <div class="fade_pop_ow1">提示</div>
                    <img src={require('../images/16.png')} class="fade_pop_owimg1" />
                    <div class="fade_pop_ow2">你所输入的验证码不正确 请重新输入</div>
                    <div class="fade_pop_ow2">请重新输入</div>
                    <div class="fade_pop_ow3" onClick= {this.fadeChange_cancel.bind()}>重新输入</div>
                </div></div>:''}
                <h1 class="login_w1">重置密码</h1>
                <div class="login_div_1">
                    <img src={require('../images/9.png')} class="login_img1" />
                    <div class="login_div_input">
                        <div class="login_div_inputInner"><input type="text" placeholder="请输入您的手机号码" /></div>
                        <div class="login_div_inputInner"><input type="text" placeholder="请输入您的验证码" />
                            <span class="codeSpan">获取验证码</span>
                        </div>
                    </div>
                    <div class="loginBtn" onClick= {this.fadeChange.bind()}>登录</div>
                    <img src={require('../images/13.png')} class="login_img3" />
                </div>
                <Link to="/Login"><div class="cancelBtn">取消</div></Link>
            </div>
        )
    }
    fadeChange= () => {
        this.setState({btnStatus: true});
    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
}