import React, { Component } from 'react';
import '../css/login.css';
import '../css/common.css';
import { Link } from 'react-router-dom';

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }
    componentWillMount(){
        var status = localStorage.clear();
        
	}
    render() {
        return (
            <div>
                 {this.state.btnStatus===true ?<div><div class="fade"></div>
                    <div class="fade_pop_o">
                        <div class="fade_pop_ow1">提示</div>
                        <img src={require('../images/16.png')} class="fade_pop_owimg1" />
                        <div class="fade_pop_ow2">账号或密码错误</div>
                        <div class="fade_pop_ow2">请重新输入</div>
                        <Link to="/IndexShow"><div class="fade_pop_ow3">重新输入</div></Link>
                    </div></div>:''}
                <h1 class="login_w1">用户登录</h1>
                <div class="login_div_1">
                    <img src={require('../images/9.png')} class="login_img1" />
                    <img src={require('../images/10.png')} class="login_img2" />
                    <div class="login_div_input">
                        <div class="login_div_inputInner"><input type="text" placeholder="请输入您的手机号码" /></div>
                        <div class="login_div_inputInner"><input type="text" placeholder="请输入您的密码" /></div>
                    </div>
                    <div class="loginBtn" onClick= {this.fadeChange.bind()}>登录</div>
                    
                </div>
                <div class="login_title">
                    <span class="login_title_l">
                        <Link to="/Reset"><span class="login_title_l">忘记密码</span></Link>
                    </span>
                    <Link to="/Regist"><div class="login_title_r">
                        <span>注册账号</span>
                        <img src={require('../images/11.png')} />
                    </div></Link>
                </div>
                <div class="login_des">
                    <span></span>
                    <font>其他登录方式</font>
                    <span></span>
                </div>
                <Link to="/Vx"><div class="login_type">
                    <img src={require('../images/12.png')} />
                    <span>微信登录</span>
                </div></Link>
            </div>
        )
    }

    fadeChange= () => {
        this.setState({btnStatus: true});
    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
}