import React, { Component } from 'react';
import '../css/login.css';
import '../css/common.css';
import { Link } from 'react-router-dom';
export default class Vx extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div>
                <h1 class="login_w1">微信登录</h1>
                <div class="login_div_1">
                    <img src={require('../images/9.png')} class="login_img1" />
                    <div class="login_div_input">
                        <div class="login_div_inputInner"><input type="text" placeholder="请输入您的手机号码" /></div>
                        <div class="login_div_inputInner"><input type="text" placeholder="请输入您的验证码" />
                            <span class="codeSpan">获取验证码</span>
                        </div>
                    </div>
                    <Link to="/Login"><div class="loginBtn">登录</div></Link>
                    <img src={require('../images/59.png')} class="login_img3" />
                </div>
                <Link to="/Login"><div class="cancelBtn">取消</div></Link>
            </div>
        )
    }
}