import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/recomm.css';
export default class Ticket extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus_1: false,
            btnStatus_2: false
        };
    }

    render() {
        return (
            <div>
                {this.state.btnStatus_1===true ?<div><div class="fade"></div>
                <div class="fade_pop_o">
                    <div class="fade_pop_ow1">提示</div>
                    <img src={require('../images/57.png')} class="fade_pop_owimg1" />
                    <div class="fade_pop_ow2">积分兑换失败</div>
                    <div class="fade_pop_ow2">积分不足</div>
                    <div class="fade_pop_ow3" onClick= {this.fadeChange_cancel.bind()}>知道了</div>
                </div></div>:''}
                {this.state.btnStatus_2===true ?<div><div class="fade"></div>
                <div class="fade_pop_o">
                    <div class="fade_pop_ow1">提示</div>
                    <img src={require('../images/57.png')} class="fade_pop_owimg1" />
                    <div class="fade_pop_ow2">积分兑换成功</div>
                    <div class="fade_pop_ow3" onClick= {this.fadeChange_cancel.bind()}>知道了</div>
                </div></div>:''}
                <div class="backHeader">
                    <a href="/Recomm"><img src={require('../images/23.png')} alt="" class="backImg" /></a>
                    <span>推荐有赏</span>
                </div>

                <div class="recomm_1">
                    当你朋友完成第一次充值后，你将会获得20积分，可以用于 换取礼品的提现
                </div>
                <div class="recomm_type">
                    <div class="recomm_type_innner">
                        <div>剩余积分</div>
                        <div>220</div>
                        <div class="line"></div>
                    </div>
                    <div class="recomm_type_innner">
                        <div>已使用</div>
                        <div>100</div>
                    </div>
                </div>
                <div class="rewrap">
                    <div class="adminInnerTitle">兑换积分</div>
                    <div class="adminInnerWrap" onClick= {this.fadeChange_1.bind()}>
                        <div class="adminDesDiv">
                            <div class="adminDesDivTop">
                                LV1收费会员
                                </div>
                            <div class="adminDesDivmiddle">
                                <span>支援4條線</span>
                                <span>否</span>
                            </div>
                            <div class="adminDesDivmiddle">
                                <span>到期日延長</span>
                                <span>30天</span>
                            </div>
                            <div class="adminDesDivmiddle">
                                <span>同時登入裝置</span>
                                <span>1台</span>
                            </div>
                            <div class="adminDesDivmiddle">
                                <span>成功次數</span>
                                <span>1次</span>
                            </div>
                            <div class="adminDesDivmiddle bt">
                                <span class="spanMoney">$6.9</span>
                                <img src={require('../images/43.png')} class="shop" alt="" />
                            </div>
                        </div>
                        <div class="adminDesDiv">
                            <div class="adminDesDivTop">
                                LV2收费会员
                                </div>
                            <div class="adminDesDivmiddle">
                                <span>支援4條線</span>
                                <span>否</span>
                            </div>
                            <div class="adminDesDivmiddle">
                                <span>到期日延長</span>
                                <span>30天</span>
                            </div>
                            <div class="adminDesDivmiddle">
                                <span>同時登入裝置</span>
                                <span>1台</span>
                            </div>
                            <div class="adminDesDivmiddle">
                                <span>成功次數</span>
                                <span>1次</span>
                            </div>
                            <div class="adminDesDivmiddle bt">
                                <span class="spanMoney">$6.9</span>
                                <img src={require('../images/43.png')} class="shop" alt="" />
                            </div>
                        </div>
                    </div>


                    <div class="adminInnerWrap" onClick= {this.fadeChange_2.bind()}>
                        <div class="adminDesDiv">
                            <div class="adminDesDivTop">
                                成功次数
                                </div>
                            <div class="adminDesDivmiddle">
                                <span>数量</span>
                                <span>1次</span>
                            </div>
                            <div class="adminDesDivmiddle bt">
                                <span class="spanMoney">$6.9</span>
                                <img src={require('../images/43.png')} class="shop" alt="" />
                            </div>
                        </div>
                        <div class="adminDesDiv">
                            <div class="adminDesDivTop">
                                人民币
                           </div>
                            <div class="adminDesDivmiddle">
                                <span>金额</span>
                                <span>10元</span>
                            </div>
                            <div class="adminDesDivmiddle bt">
                                <span class="spanMoney">$6.9</span>
                                <img src={require('../images/43.png')} class="shop" alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )


    }
    fadeChange_1= () => {
        this.setState({btnStatus_1: true});
    }
    fadeChange_2= () => {
        this.setState({btnStatus_2: true});
    };
    fadeChange_cancel= () =>{
        this.setState({btnStatus_1: false,btnStatus_2: false});
    }
}