import React, { Component } from 'react';
import { BrowserRouter as Router, Route ,NavLink} from 'react-router-dom'
import '../css/indexShow.css';
import '../css/common.css';
import message from '../components/message';
import shop from '../components/shop';
import ticketGet from '../components/ticketGet';
import setting from '../components/setting';

export default class IndexShow extends Component {
    constructor(props) {
        super(props);
        this.state = {
            routeStatus:""
        };
    }
    componentWillMount(){
        var status = localStorage.getItem("selectStatus");
        
        this.setState({routeStatus: status});
	}
    render() {
        return (
            <Router>
                <div className="App">
                    <div class="content_div">
                        {this.state.routeStatus==null ?<Route path="/indexShow" exact component={message} />:''}
                        {this.state.routeStatus==2 ?<Route path="/indexShow" exact component={message} />:''}
                        {this.state.routeStatus==3 ?<Route path="/indexShow" exact component={shop} />:''}
                        {this.state.routeStatus==4 ?<Route path="/indexShow" exact component={ticketGet}/>:''}
                        {this.state.routeStatus==5 ?<Route path="/indexShow" exact component={setting} />:''}
                        {this.state.routeStatus==2 ?<Route path="/message" exact component={message} />:''}
                        {this.state.routeStatus==3 ?<Route path="/Shop" exact component={shop} />:''}
                        {this.state.routeStatus==4 ?<Route path="/TicketGet" exact component={ticketGet}/>:''}
                        {this.state.routeStatus==5 ?<Route path="/Setting" exact component={setting} />:''}
                    </div>
                    <div class="tabbar_div">
                        <NavLink to="/message" className={this.state.routeStatus==null||this.state.routeStatus==2?'activesN2':''} activeClassName="activesN" onClick= {this.tabFn2.bind(2)}>
                            <div class="tabbar_divInner">
                                <div class="tabbar_img tab_img1"></div>
                                <span>消息</span>
                            </div>
                        </NavLink>
                        <NavLink to="/Shop" className={this.state.routeStatus==3?'activesN2':''} activeClassName="activesN" onClick= {this.tabFn3.bind()}>
                            <div class="tabbar_divInner">
                                <div class="tabbar_img tab_img2"></div>
                                <span>商城</span>
                            </div>
                        </NavLink>
                        <NavLink to="/TicketGet" className={this.state.routeStatus==4?'activesN2':''} activeClassName="activesN" onClick= {this.tabFn4.bind()}>
                            <div class="tabbar_divInner">
                                <div class="tabbar_img tab_img3"></div>
                                <span>抢票</span>
                            </div>
                        </NavLink>
                        <NavLink to="/Setting" className={this.state.routeStatus==5?'activesN2':''} activeClassName="activesN" onClick= {this.tabFn5.bind()}>
                            <div class="tabbar_divInner">
                                <div class="tabbar_img tab_img4"></div>
                                <span>设定</span>
                            </div>
                        </NavLink>
                    </div>
                </div>
            </Router>
        )
    }

    fadeChange = () => {
    }
    tabFn2= () =>{
        localStorage.setItem("selectStatus",2);
        var status = localStorage.getItem("selectStatus");
        this.setState({routeStatus: status});
    }
    tabFn3= () =>{
        localStorage.setItem("selectStatus",3);
        var status = localStorage.getItem("selectStatus");
        this.setState({routeStatus: status});
    }
    tabFn4= () =>{
        localStorage.setItem("selectStatus",4)
        var status = localStorage.getItem("selectStatus");
        this.setState({routeStatus: status});
    }
    tabFn5= () =>{
        localStorage.setItem("selectStatus",5)
        var status = localStorage.getItem("selectStatus");
        this.setState({routeStatus: status});
    }
}