import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/recomm.css';
export default class Ticket extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }

    render() {
        return (
            <div>
                <div class="backHeader">
                    <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                    <span>推荐有赏</span>
                </div>
                
                <div class="recomm_1">
                    当你朋友完成第一次充值后，你将会获得20积分，可以用于 换取礼品的提现
                </div>
                <div class="recomm_share">
                    <div class="shareD">分享你的链接</div>
                    <div class="shareN">
                        <div >http://fsdlk.com/dainsaad</div>
                        <img src={require('../images/51.png')} alt=""/>
                        <img src={require('../images/52.png')} alt=""/>
                    </div>
                </div>
                
                <div class="recomm_type">
                    <div class="recomm_type_innner">
                        <div>已成功邀请</div>
                        <div>5人</div>
                        <div class="line"></div>
                    </div>
                    <div class="recomm_type_innner">
                        <div>剩余积分</div>
                        <div>100</div>
                    </div>
                </div>
                <a href="/Recomm_2"><div class="exitLogin">进入积分商城</div></a>
            </div>
        )


    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
}