import React, { Component } from 'react';
import '../css/login.css';
import '../css/common.css';
import { Link } from 'react-router-dom';
export default class Regist extends Component {
    constructor(props) {
        super(props);
        this.state = {menuStatus: false};
    }

    render() {
        return (
            <div>
                <h1 class="login_w1">注册账号</h1>
                <div class="login_div_1">
                    <img src={require('../images/9.png')} class="login_img1" />
                    <div class="login_div_input registLogin_div_input">
                        <div class="login_div_inputInner regist_inner"><input type="text" placeholder="请输入您的手机号码" /></div>
                        <div class="login_div_inputInner regist_inner"><input type="text" placeholder="请输入您的验证码" />
                            <span class="codeSpan">获取验证码</span>
                        </div>
                        <div class="login_div_inputInner regist_inner"><input type="text" placeholder="请输入您的密码" /></div>
                        <div class="login_div_inputInner regist_inner"><input type="text" placeholder="请选择您的地区" />
                            <span class="codeSpan"><span  onClick= {this.menuChange.bind()}>地区1</span>
                            <img src={require('../images/15.png')} class="dqImg"  onClick= {this.menuChange.bind()}/>

                            {this.state.menuStatus===true ?<div class="menuDiv">
                                <div onClick= {this.menuCancelChange.bind()}>地区一</div>
                            </div>:''}
                            </span>
                        </div>
                    </div>
                    <Link to="/Login"><div class="loginBtn">提交</div></Link>
                    <img src={require('../images/14.png')} class="login_img3" />
                </div>
                <Link to="/Login"><div class="cancelBtn">取消</div></Link>
            </div>
        )
    }
    menuCancelChange= () =>{
        this.setState({menuStatus: false});
    }
    menuChange= () =>{
        this.setState({menuStatus: true});
    }
}